.skills {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  text-align: center;
}

.skills-wrapper {
  -ms-flex: 1 1;
      flex: 1 1;
  border-right: 1px dashed #bbb;
}

.skills-wrapper:last-child {
  border: none;
}

.skills-wrapper h4{
  color: #000;
  font-size: 1.3em;
  font-weight: 500;
  margin-bottom: 20px;
}

.skills-wrapper > div{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.skill-wrapper {
  padding: 15px;
}

.skill-wrapper img {
  height: 70px;
}

@media (max-width: 768px) {
  .skills {
    display: inline-block;
  }
  .skills-wrapper {
    border: none;
    margin-bottom: 20px;
  }
}



